import React, { Component } from 'react';

class About extends Component {

	showMe = () => {
		if (this.props.show) {
			return "About"
		} else {
			return "About hidden"
		}
	}
	render() {
		return (
			<div className={this.showMe()}>
				About
			</div>
		);
	}
}

export default About;
