import React, { Component } from 'react';

class Explore extends Component {

	showMe = () => {
		if (this.props.show) {
			return "Explore"
		} else {
			return "Explore hidden"
		}
	}
	render() {
		return (
			<div className={this.showMe()}>
				Explore
			</div>
		);
	}
}

export default Explore;
